#drum-machine {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #282c34;
    color: white;
  }
  
  #display {
    width: 50%;
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 20px;
    background-color: #333;
    padding: 10px;
    border-radius: 8px;
  }
  
  .pads-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  
  .drum-pad {
    background-color: #444;
    color: white;
    padding: 30px;
    text-align: center;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1.5rem;
    transition: background-color 0.2s;
  }
  
  .drum-pad:hover {
    background-color: #555;
  }
  
  .clip {
    display: none;
  }
  